<template>
  <div class="upload-cover" @click="showCoverSelect">
    <div class="cover-wrap">
      <!-- srccoverImage dataprops-->
      <el-image
        class="cover-image"
        ref="cover-image"
        style="width: 200px; height: 200px"
        :src='coverImage'
        fit="cover">
        </el-image>
    </div>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="50%"
        append-to-body>
        <!-- tab -->
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="" name="first">
            <!-- ref
            1.dom
            2. -->
            <image-list
            :is-show-add="false"
            :is-show-action="false"
            is-show-selected
            ref="image-list"
             />
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <input
              type="file"
              ref="file"
              @change='onFileChagne()'>

              <img
              class="cover-wrap"
              @click="$refs.file.click()"
              src=""
              alt=""
              ref='preview-image'>
            </el-tab-pane>
        </el-tabs>

    <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
        type="primary"
        @click="onCoverComfirm()"
        > </el-button>
    </span>
</el-dialog>

  </div>
</template>

<script>
import { uploadImage } from '@/api/image'
import ImageList from '@/views/image/components/image-list'
export default {
  name: 'UploadCover',
  components: {
    ImageList
  },
  props: ['cover-image'], // 
  data () {
    return {
      dialogVisible: false,
      activeName: 'first',
      url: ''
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    showCoverSelect () {
      this.dialogVisible = true
    },
    handleClick (tab, event) {
      console.log(tab, event)
    },
    onFileChagne () {
      // console.log('hahah')
      // 1. 
      const file = this.$refs.file.files[0]

      // 2. 
      const blob = window.URL.createObjectURL(file)
      this.$refs['preview-image'].src = blob
    },
    onCoverComfirm () {
      // tab input-file 
      if (this.activeName === 'second') {
        const file = this.$refs.file.files[0]
        if (!file) { // 
          this.$message('')
          return
        }
        //    image
        const fd = new FormData()
        fd.append('image', file)
        uploadImage(fd).then(res => {
          // console.log(res)
          // 
          this.dialogVisible = false
          // 
          // this.$refs['cover-image'].src = res.data.data.url
          this.url = res.data.data.url

          // ()
          this.$emit('update-cover', res.data.data.url)
        })
      } else if (this.activeName === 'first') {
      // 
        const imageList = this.$refs['image-list']
        // console.log(imageList.selected) // 
        const selected = imageList.selected
        if (selected === null) {
          this.$message('')
          return
        }
        // 
        this.dialogVisible = false
        // console.log(imageList.images[selected].url)
        // ()
        this.$emit('update-cover', imageList.images[selected].url)
      }
    }
  }
}

</script>
<style scoped lang='less'>
.cover-wrap{
  float: left;
  margin-right: 10px;
  height: 200px;
  width: 200px;
  border: 1px dashed #ccc;
  background:url(../pic_bg.png) no-repeat;
  background-size: cover;
}
</style>
