<template>
  <div class="image-list">
       <!--  -->
   <div class="top">
    <el-radio-group v-model="collect" @change="onCollectChange">
      <el-radio-button :label="false" >
        </el-radio-button>
      <el-radio-button :label="true">
        </el-radio-button>
    </el-radio-group>
     <el-button size="small"
     type="warning"
     @click="dialogimgleVisible = true"
     v-if='isShowAdd'
    ></el-button>
  </div>
  <!--  -->
  <el-row :gutter="10">
  <el-col
    v-for="(img, index) in images"
    :key=index
    :xs="24" :sm="12" :md="8" :lg="6" :xl="4"
    @click.native="selected = index">
      <div class="demo-image">
    <el-image
      style="height: 150px"
      :src="img.url"
      fit="cover">
      </el-image>
      <!--  -->
    <div
    class= "selected"
    v-if= "isShowSelected && selected === index"
    >
    </div>
      <!--  -->
     <div class="little" v-if='isShowAction'>
       <el-button
       size="mini"
       circle
      type="success"
      :icon="img.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'"
      @click="oncllectImg(img)"
      :loading="img.loading"
      ></el-button>
      <el-button
      size="mini"
      circle
      icon='el-icon-delete'
      type="danger"
      @click="ondeleteImg(img.id)"
      :loading="img.loading"
       ></el-button>
         <!-- <i :class='{
             //  el-icon-star-on
             // false   true
             "el-icon-star-off": !img.is_collected,
             "el-icon-star-on": img.is_collected,
             "star":true
         }' @click="oncllectImg(img)"></i>
         <i class='el-icon-delete' @click="ondeleteImg(img.id)"></i> -->
     </div>
</div>
  </el-col>
</el-row>
<!--  -->
<el-pagination
  style="margin-top:20px"
  background
  layout="prev, pager, next"
  :total="totalCount"
  :page-size="pageSize"
  :current-page.sync="page"
  @current-change="onCurrentChange">
</el-pagination>

<!--  -->
<el-dialog title=""
:visible.sync="dialogimgleVisible"
:append-to-body="true">
    <!-- name 
    show-file-list  true
    on-success 
    v-if="dialogimgleVisible   -->
    <!-- upload  
    action ,
    headers
    -->
  <el-upload
    v-if="dialogimgleVisible"
    class="upload-demo"
    drag
    action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
    multiple
    :headers="uploadHeaders"
    name="image"
    :on-success="onUploadSuccess"
    >
    <i class="el-icon-upload"></i>
    <div class="el-upload__text"><em></em></div>
    <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
  </el-upload>
</el-dialog>
  </div>
</template>

<script>
import {
  getImages, // 
  deleteImage, // 
  collectImage // 
} from '@/api/image/'

export default {
  name: 'ImageList',
  components: {},
  props: {
    // 
    isShowAdd: {
      type: Boolean, // 
      default: true // 
    },
    // 
    isShowAction: {
      type: Boolean, // 
      default: true // 
    },
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false,
      images: [],
      dialogimgleVisible: false,
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      pageSize: 10, // 
      totalCount: 0, // 
      selected: null // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadImages(false, 1)
  },
  mounted () {},
  methods: {
    // 
    loadImages (collect = false, page = 1) {
      // 
      this.page = page
      getImages({
        collect,
        page,
        per_page: this.pageSize
      }).then(res => { //   collect
        // // console.log(res)
        // this.images = res.data.data.results
        // const { results, total_count: totalCount } = res.data.data
        // this.images = results
        // this.totalCount = totalCount
        const results = res.data.data.results
        results.forEach(img => {
          // img  loading 
          //  loading 
          img.loading = false
        })
        this.images = results
        this.totalCount = res.data.data.total_count
      })
    },
    onCollectChange (value) {
      this.loadImages(value)
    },
    onUploadSuccess () {
      // 
      this.dialogimgleVisible = false
      //  ()
      this.loadImages(false)
    },
    // 
    onCurrentChange (page) {
      this.loadImages(false, page)
    },

    ondeleteImg (imageId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // console.log(imageId)
        deleteImage(imageId).then(() => {
          this.loadImages()
          this.$message({
            type: 'success',
            message: '!'
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },

    oncllectImg (img) {
      img.loading = true // loading
      // console.log(img)
      collectImage(img.id, !img.is_collected).then(() => {
        // console.log(res)
        img.is_collected = !img.is_collected
        img.loading = false // loading
      })
    }
  }
}

</script>
