<template>
    <div class="login-container">
        <div class="login-form-wrap">
            <!--  -->
          <div class="login-head">
            <div class="logo"></div>
          </div>
        <!--
          el-form 
           el-form-item 

          
          1.el-form  model
          2.el-form-item prop
          prop
          3. el-form rules

          :
          1.el-form ref
          2. ref  el-form  validata
         -->
        <el-form class="login-form" ref="login-form" :model="user" :rules="formRules">
            <el-form-item prop="mobile">
                <el-input  v-model="user.mobile" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="code">
                <el-input v-model="user.code" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="agree">
                <el-checkbox class="login-text" v-model="user.agree"></el-checkbox>
            </el-form-item>
            <el-form-item>
                <el-button  class="login-btn" type="primary" @click="onLogin" :loading="loginLoading"></el-button>
            </el-form-item>
        </el-form>
        </div>
    </div>
</template>

<script>
import { login } from '@/api/user'
export default {
  name: 'LoginIndex',
  components: {},
  props: {},
  data () {
    return {
      user: {
        mobile: '', // 
        code: '', // 
        agree: ''
      },
      loginLoading: false,
      formRules: { // 
      //  []
        mobile: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^1[3|5|7|8]\d{9}$/, message: '', trigger: 'change' }
        ],
        code: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^\d{6}$/, message: '', trigger: 'change' }
        ],
        agree: [
          {
            // 
            // rule ,vulue true/false
            // callback() 
            // callback(new Error(''))  
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
            },
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    onLogin () {
      // 
      // const user = this.user
      // 
      // validata   valid true false   err
      this.$refs['login-form'].validate(valid => {
        if (!valid) {
        // console.log('')
        //  
          return
        }
        // ,
        this.login()
      })
    },
    login () {
      //  loading...
      this.loginLoading = true
      // 
      login(this.user).then(res => {
        // console.log(res)
        this.$message({
          message: ', ',
          type: 'success'
        })
        //  loading
        this.loginLoading = false
        // 
        // window.localStorage.setItem('user', res.data.data)
        // json user.js 
        window.localStorage.setItem('user', JSON.stringify(res.data.data))
        //    name
        // this.$router.push('/')

        this.$router.push({
          name: 'home'
        })
        // 
      }).catch(err => {
        console.log('', err)
        this.$message.error(', ')
        this.loginLoading = false
      })
    }
  }
}
</script>

<style scoped lang="less">
    .login-container{
        // 
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        // flex 
        display: flex;
        flex-direction: column;
        // 
        justify-content: center;
        // 
        align-items: center;
        // 
        background: url("./login_bg.jpg") no-repeat;
        background-size: cover;
        .login-form-wrap{
            min-width: 300px;
            padding: 30px 50px;
            background-color: #fff;
            .login-head{
                display: flex;
                justify-content: center;
                .logo{
                width: 200px;
                height: 60px;
                background: url("./logo_index.png") no-repeat;
                background-size: contain;
                }
            }}
        .login-form{
            .login-text{
                color: #000;
            }
            .login-btn{
                width: 100%;
            }
        }
    }
</style>
