<template>
  <div class="setting-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-row>
        <el-col :span="12">
      <div class="text">
        <el-form ref="form" :model="user" label-width="80px">
          <el-form-item label=":">{{user.id}}
          </el-form-item>
            <el-form-item label=":">{{user.mobile}}
          </el-form-item>
            <el-form-item label=":">
            <el-input v-model="user.name"></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-input type="textarea" v-model="user.intro"></el-input>
          </el-form-item>
            <el-form-item label=":">
            <el-input v-model="user.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
          :loading="updateProfileLoading"
          type="primary" @click="onSaveSetting"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!-- vue dom
      vue  ref
      vue $refs.file dom -->
      </el-col>
        <el-col :offset="4" :span="8">
        <!-- <el-avatar
        shape="square"
        :size="200"
        fit="cover"
        :src="user.photo">
        </el-avatar> -->
        <!-- <p @click = "$refs.file.click()"></p>  input ref  -->
        <!-- <label for="file"></label>  input id-->
        <!--  -->
        <label for="file">
          <el-avatar
            shape="square"
            :size="200"
            fit="cover"
            :src="user.photo"
          >
          </el-avatar>
            <p></p>
        </label>
        <input
          id="file"
          type="file"
          v-show='false'
          ref="file"
          @change=onFileChange()
        >
        </el-col>
      </el-row>
      <el-dialog
        append-to-body
        title=""
        :visible.sync="dialogVisible"
        @opened='onPropImage'
        @closed='onDialogClosed'>
      <div class="preview-image-wrap">
        <img
          class="preview-image"
          :src="previewImage"
          ref="preview-image"
        >
      </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button
          :loading="updatePhotoLoading"
          type="primary"
          @click="onUpdataPhoto"
          > </el-button>
      </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import {
  getUserProfile,
  updataUserPhoto,
  updataUserProfile
} from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
// 
import globalBus from '@/utils/global-bus'

export default {
  name: 'setting',
  components: {},
  props: {},
  data () {
    return {
      form: {
        name: '',
        desc: ''
      },
      user: {},
      dialogVisible: false, // 
      previewImage: '', // 
      cropper: null, // 
      updatePhotoLoading: false,
      updateProfileLoading: false,
      background: true
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadUser()
  },
  mounted () {},
  methods: {
    loadUser () {
      getUserProfile().then(res => {
        // console.log(res)
        this.user = res.data.data
      })
    },
    onFileChange () {
      // 
      const file = this.$refs.file
      const blobData = window.URL.createObjectURL(file.files[0])
      // console.log(blobData)
      this.previewImage = blobData
      // console.log('haha')

      //  
      this.dialogVisible = true
      //  change
      // value
      this.$refs.file.value = ''
    },
    onPropImage () {
      // dom
      const image = this.$refs['preview-image']
      // 
      // 1.
      // 2. replace 

      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return // true
      }
      // 
      this.cropper = new Cropper(image, {
        // aspectRatio: 16 / 9,
        // crop (event) {
        //   console.log(event.detail.x)
        //   console.log(event.detail.y)
        //   console.log(event.detail.width)
        //   console.log(event.detail.height)
        //   console.log(event.detail.rotate)
        //   console.log(event.detail.scaleX)
        //   console.log(event.detail.scaleY)
        // }

        // 
        aspectRatio: 1, // 
        // aspectRatio: 2 / 4,
        viewMode: 1, // 1, 0, 2
        dragMode: 'none', //  none
        cropBoxResizable: false
      })
    },
    // 1.
    onDialogClosed () { // 
      // 
      // datacoppernull cropper
      this.cropper.destroy()
    },
    onUpdataPhoto () {
      this.updatePhotoLoading = true
      // 
      this.cropper.getCroppedCanvas().toBlob(file => {
        // console.log(file)
        const fd = new FormData()
        // fd.append(,file)
        fd.append('photo', file)
        // fd
        updataUserPhoto(fd).then(res => {
          // console.log(res)
          // 
          this.dialogVisible = false
          // 
          // this.loadUser()

          //  blob 
          this.user.photo = window.URL.createObjectURL(file)

          // 
          // this.user.photo = res.data.data.photo

          //  loading
          this.updatePhotoLoading = false

          this.$message({
            type: 'success',
            message: ''
          })

          // 
          globalBus.$emit('update-user', this.user)
        })
      })
    },
    onSaveSetting (data) {
      this.updateProfileLoading = true
      // 
      const { name, intro, email } = this.user
      updataUserProfile({
        name,
        intro,
        email
      }).then(res => {
        this.$message({
          type: 'success',
          message: ''
        })
        this.updateProfileLoading = false

        // ()
        // 
        globalBus.$emit('update-user', this.user)
      })
    }
  }
}

</script>
<style scoped lang='less'>
.preview-image-wrap{
.preview-image{
  /* Ensure the size of the image fit the container perfectly */
    display: block;

    /* This rule is very important, please don't ignore this */
    max-width: 100%;
    height: 200px;
}
}
</style>
