<template>
  <div class="article-container" >
    <el-card class="filter-card">
  <div slot="header" class="clearfix">
    <span>
       <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    </span>
  </div>
  <!--  -->
    <!-- size  label-width="80px"  -->
    <el-form ref="form" :model="form" label-width="50px" size="mini">
  <el-form-item label=" :">
    <!-- form.status 
    label radiovalue -->
    <el-radio-group v-model="status">
      <el-radio :label="null"></el-radio>
      <el-radio :label="0"></el-radio>
      <el-radio :label="1"></el-radio>
      <el-radio :label="2"></el-radio>
      <el-radio :label="3"></el-radio>
      <el-radio :label="4"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label=" :">
    <el-select v-model="channelId" placeholder="">
      <el-option
      label=""
      :value="null">
      </el-option>
      <!-- channel  channels  -->
      <el-option
        :label = "channel.name"
        :value = "channel.id"
        v-for = "(channel, index) in channels "
        :key = 'index'
      ></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label=" :">
    <el-date-picker
      v-model="rangeDate"
      type="daterange"
      range-separator=""
      start-placeholder=""
      end-placeholder=""
        :default-time="['12:00:00']"
        format="yyyy-MM-dd"
        value-format="yyyy-MM-dd"
      />
  </el-form-item>

  <el-form-item>
    <!-- buttonclick -->
    <el-button
    type="primary"
    :disabled="loading"
    @click="loadArticles(1)"></el-button>
  </el-form-item>
  </el-form>
</el-card>

<!--  -->
<el-card class="bottom-card">
  <div slot="header" class="clearfix" size="small">
     {{totalCount}} 
  </div>
  <!-- el-table data
       :data="articles"
       v-for 
       prop 
   -->
  <el-table
    v-loading="loading"
    :data="articles"
    stripe
    style="width: 100%"
    class="list-table"
    size='small'>
    <el-table-column
      prop=""
      label="">
      <template slot-scope="scope">
        <!--   -->
         <img v-if="scope.row.cover.images[0]" class="article-cover"
        :src="scope.row.cover.images[0]" alt="">

        <img v-else class="article-cover"
        src="./error.gif" alt="">
      </template>
    </el-table-column>
    <el-table-column
      prop="title"
      label="">
    </el-table-column>
    <el-table-column
      prop="status"
      label="">
      <!-- template
      slot-scope="scope"  scope  scope.row -->
    <template slot-scope="scope">
      <el-tag :type='articleStatus[scope.row.status].type'>{{ articleStatus[scope.row.status].text}}</el-tag>
      <!-- <el-tag v-if="scope.row.status === 0"></el-tag>
      <el-tag v-else-if="scope.row.status === 1" type="warning"></el-tag>
      <el-tag v-else-if="scope.row.status === 2" type="success"></el-tag>
      <el-tag v-else-if="scope.row.status === 3" type="danger"></el-tag>
      <el-tag v-else-if="scope.row.status === 4" type="info"></el-tag> -->
    </template>
    </el-table-column>
    <el-table-column
      prop="pubdate"
      label="">
    </el-table-column>

    <!--  -->
       <!-- circle -->
     <el-table-column label="">
      <template slot-scope="scope">
        <!-- 
        publish
        toString() -->
        <el-button
          size="mini"
          type="primary"
          icon="el-icon-edit"
          plain
          circle
          @click="$router.push('/publish?id='+scope.row.id.toString())"
          ></el-button>
        <!--  -->
        <el-button
          size="mini"
          type="danger"
          icon="el-icon-delete"
          plain
          circle
          @click="onDeleteArticle(scope.row.id)"
          ></el-button>
      </template>
    </el-table-column>
  </el-table>
     <!--  -->
     <!-- :current-page.sync='page'  -->
  <el-pagination
    background
    layout="prev, pager, next"
    :total="totalCount"
    @current-change="onCurrentChange"
    :page-size='pageSize'
    :disabled="loading"
    :current-page.sync='page'
    >
  </el-pagination>
  <!-- totalCount 
    current-change -->
</el-card>
  </div>
</template>

<script>
// 
import {
  getArticles,
  getArticleChannels,
  deleteArticle
} from '@/api/article'

export default {
  name: 'ArticleIndex',
  components: {},
  props: {},
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518'
      }, {
        date: '2016-05-04',
        name: '',
        address: ' 1517'
      }, {
        date: '2016-05-01',
        name: '',
        address: ' 1519'
      }, {
        date: '2016-05-03',
        name: '',
        address: ' 1516'
      }],
      articles: [], // 
      // 
      articleStatus: [
        { status: 0, text: '', type: '' },
        { status: 1, text: '', type: 'warning' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'danger' },
        { status: 4, text: '', type: 'info' }
      ],
      totalCount: 0, // 
      pageSize: 10, // 
      status: null, //  null
      channels: [], // 
      channelId: null, // 
      rangeDate: null,
      loading: true,
      page: 1
    }
  },
  computed: {},
  watch: {},
  created () {
  // 
  // create  
  // 
    this.loadArticles(1)
    this.loadChannels() // 
  },
  mounted () {},
  methods: {
    // loadArticles 
    // 1 page
    loadArticles (page = 1) {
      // 
      this.page = page
      this.loading = true
      // getArticles 
      // getArticles 
      getArticles({
        // page:1, //
        page,
        // per_page: 10 // 
        per_page: this.pageSize,
        status: this.status, // 
        channel_id: this.channelId,
        // 
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null,
        // 
        end_pubdate: this.rangeDate ? this.rangeDate[1] : null
      }).then(res => {
        // console.log(res)
        // this.articles = res.data.data.results
        // this.totalCount=res.data.data.total_count
        // ES6
        const { results, total_count: totalCount } = res.data.data
        this.articles = results
        this.totalCount = totalCount
        this.loading = false
      })
    },
    onSubmit () {
      // console.log('submit!')
    },

    // page
    onCurrentChange (page) {
      // console.log(page) // 
      this.loadArticles(page)
    },

    loadChannels () {
      getArticleChannels().then(res => {
        // console.log(res)
        this.channels = res.data.data.channels
      })
    },

    onDeleteArticle (articleId) {
      // console.log(articleId)
      // console.log(articleId.toString())
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => { // 
        // deleteArticle(articleId).then(res => {
        deleteArticle(articleId.toString()).then(res => {
          // console.log(res)
          //  
          this.loadArticles(this.page)
        })
      }).catch(() => { // 
        this.$message({
          type: 'info',
          message: ''
        })
      })
      // console.log('onDeleteArticle')
    }
  }
}

</script>
