<template>
  <div class="comment-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    <el-table
      :data="comment"
      style="width: 100%">
      <el-table-column
        prop="title"
        label="">
      </el-table-column>
      <el-table-column
        prop="total_comment_count"
        label="">
      </el-table-column>
      <el-table-column
        prop="fans_comment_count"
        label="">
      </el-table-column>
      <el-table-column
        prop="comment_status"
        label="">
        <template slot-scope="scope">
          {{scope.row.comment_status ? '' :''}}
        </template>
      </el-table-column>
      <el-table-column
        prop="address"
        label="">
       <template slot-scope="scope">
        <el-switch
          v-model="scope.row.comment_status "
          active-color="#13ce66"
          inactive-color="#ff4949"
          @change="onStatusChange(scope.row)">
        </el-switch>
       </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="block">
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="page"
      :page-sizes="[10, 20, 50, 100]"
      :page-size.sync="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalCount">
    </el-pagination>
  </div>
    </el-card>
  </div>
</template>

<script>
import {
  getComment,
  updataCommentStatus
} from '@/api/comment'
export default {
  name: 'comment',
  components: {},
  props: {},
  data () {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 '
      }],
      comment: [], // 
      pageSize: 20,
      totalCount: 0,
      page: 1

    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadArticles()
  },
  mounted () {},
  methods: {
    loadArticles (page = 1) {
      //  
      this.page = page
      getComment({
        page,
        per_page: this.pageSize, // 10
        response_type: 'comment'
      }).then(res => {
        // console.log(res)
        this.comment = res.data.data.results
        this.totalCount = res.data.data.total_count
      })
    },
    // pageSize  ()
    handleSizeChange (page) {
      this.loadArticles(page)
    },
    // currentPage  ()
    handleCurrentChange (page) {
      this.loadArticles(page)
    },
    onStatusChange (comment) {
      updataCommentStatus(comment.id.toString(), comment.comment_status).then(res => {
        // console.log(res)
        this.$message({
          type: 'success',
          message: res.allow_comment ? '' : ''
        })
      })
    }
  }
}
</script>

