<template>
  <div class="publish-container">
<el-card class="box-card">
    <div slot="header" class="clearfix">
    <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
    <el-breadcrumb-item>{{$route.query.id ? '' : ''}}</el-breadcrumb-item>
    </el-breadcrumb>
      </div>
      <!-- prop  ref  -->
    <el-form ref="publish-form" :model="article" label-width="70px" :rules="ruleForm">
    <el-form-item label=":" prop="title">
        <el-input v-model="article.title"></el-input>
    </el-form-item>
    <el-form-item label=":" prop="content">
      <div>
          <el-tiptap
            v-model="article.content"
            :extensions="extensions"
            height = "300"
            placeholder = ''
          />
        </div>
    </el-form-item>
    <!--  -->
    <el-form-item label=":">
      <el-radio-group v-model="article.cover.type">
        <el-radio :label="1"></el-radio>
        <el-radio :label="3"></el-radio>
        <el-radio :label="0"></el-radio>
        <el-radio :label="-1"></el-radio>
      </el-radio-group>

<!-- article.cvoer.image -->

      <!-- 0  -->
      <template v-if="article.cover.type>0">
        <upload-cover
        v-for="(cover,index) in article.cover.type"
        :key="cover"
        @update-cover='onUpdateCover(index,$event)'
        :cover-image="article.cover.images[index]"
        />
      </template>
    </el-form-item>
    <el-form-item label=":" prop="channel_id">
        <el-select v-model="article.channel_id" placeholder="">
        <el-option
        v-for="(channel, index) in channels"
        :key="index"
        :label="channel.name"
        :value="channel.id"></el-option>
        </el-select>
    </el-form-item>
    <el-form-item>
        <el-button type="primary" @click = 'onPublish(false)'></el-button>
        <el-button @click = 'onPublish(true)'></el-button>
    </el-form-item>
</el-form>
</el-card>
  </div>
</template>

<script>
import UploadCover from './components/upload-cover'
// 
import {
  getArticleChannels,
  addArticle,
  getArticle, // 
  updateArticle // 
} from '@/api/article'

import {
  // 
  ElementTiptap,
  // necessary extensions
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  Preview,
  TextColor,
  Image
} from 'element-tiptap'

// import element-tiptap 
import 'element-tiptap/lib/index.css'
import { uploadImage } from '@/api/image'
export default {
  name: 'PublishIndex',
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },

  props: {},
  data () {
    return {
      channels: [], // 
      article: {
        title: '', // 
        content: '', // 
        cover: { // 
          type: 0, // 
          images: [] // 
        },
        channel_id: null // id
      },
      // 
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }), // render command-button in bubble menu.
        new Underline(),
        new Italic(),
        new Strike(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new Preview(),
        new TextColor(),
        new Image({
          // base64 
          uploadRequest (file) {
            // 
            const fd = new FormData()
            fd.append('image', file)
            //  return Promise 
            //  axios  Proimse 
            return uploadImage(fd).then(res => {
              // console.log(res)
              //  return 
              return res.data.data.url
            })
          } // Promise<url>
        })
      ],
      ruleForm: {
        // blur   change
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [{
          validator (rule, value, callback) {
            if (value === '<p></p>' || value === '') {
              callback(new Error('')) // 
            } else {
              callback() // 
            }
          }
        },
        { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '' }
        ]
      }
    }
  },
  computed: {
  },
  watch: {},
  created () { // 
    // 
    this.loadChannels()
    //   id 
    if (this.$route.query.id) {
      this.loadArticle()
    }
  },
  mounted () {},
  methods: {
    loadChannels () {
      getArticleChannels().then(res => {
        // console.log(res)
        this.channels = res.data.data.channels
      })
    },
    // onPublish (draft = false) {
    //   // 
    //   // 
    //   // 
    //   const articleId = this.$route.query.id
    //   // id  
    //   if (articleId) {
    //     updateArticle(articleId, this.article, draft).then(res => {
    //       // console.log(res)
    //       this.$message({
    //         message: '',
    //         type: 'success'
    //       })
    //       // 
    //       this.$router.push('/article')
    //     })
    //   } else {
    //     addArticle(this.article).then(res => {
    //       // 
    //       // console.log(res)
    //       this.$message({
    //         message: `${draft ? '' : ''}`,
    //         type: 'success'
    //       })
    //       // 
    //       this.$router.push('/article')
    //     })
    //   }
    // },

    // 
    onPublish (draft = false) {
      this.$refs['publish-form'].validate(valid => {
        if (!valid) { //  
          return
        }
        // 
        const articleId = this.$route.query.id
        // id  
        if (articleId) {
          updateArticle(articleId, this.article, draft).then(res => {
            // console.log(res)
            this.$message({
              message: '',
              type: 'success'
            })
            // 
            this.$router.push('/article')
          })
        } else {
          addArticle(this.article).then(res => {
            // 
            // console.log(res)
            this.$message({
              message: `${draft ? '' : ''}`,
              type: 'success'
            })
            // 
            this.$router.push('/article')
          })
        }
      })
    },

    //  
    loadArticle () {
      getArticle(this.$route.query.id).then(res => {
        // console.log(res)
        this.article = res.data.data
      })
    },
    onUpdateCover (index, url) {
      // console.log(url)
      this.article.cover.images[index] = url
    }
  }
}

</script>
